
.mainContainer {
  height: 100vh;
  width: 100%;
}

.navbar {
  max-width: 100%;
  height: 60px;
  background-color: white;
  padding: 0 20px;
  z-index: 200;
}


.buttonsContainer {
  width: 90%;
  height: 100%;
  z-index: 200;
  margin: auto;
  padding: 0;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.logoContainer {
  flex: 1;
  margin: auto 0;
  width: 50%;
}

.logo {
  height: 35px;
  flex: 1;
}

.headerLinks {
  margin: auto 0;
}

a {
  color: black;
  font-size: 0.9em;
  padding: 20px;

}

a:hover {
  text-decoration: none;
}

.console {
  flex: 1;
  width: 50%;
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
}

.console > img {
  padding: 0 8px;
  margin: auto 0;
}

.search-bar {
  width: 100%;
  height: 100%;
  border: none;

  text-transform: uppercase;
  font-size: 12px;
  letter-spacing: 2px;
  color: rgba(0,0,0,0.9)
}

.search-bar::placeholder {
  text-transform: uppercase;
  font-size: 12px;
  letter-spacing: 2px;
  color: rgba(0,0,0,0.9)
}

.search-bar:focus {
  border-style: none;
  outline: none;
}

.dropDownMenu {
  position: absolute;
  top: 60px;
  left: 0;
  z-index: 100;
  overflow: hidden;
  width: 100%;
  max-height: 0%;
  background-color: white;
  box-shadow:0px 0px 10px gray;
  transition: max-height 0.5s ease-in
}

.dropDownMenuOpen {
  /* transform: translateY(0); */
  max-height: 200vh;
  background-color: rgba(53,104,174,1);

}

.dropDownLinksContainer {
  width: 100%;
  padding: 5%;
  background-color: white;
  border: white 1px solid;
  margin-top: 3px;
}

.dropDownLink {
  padding: 10px 5%;
  text-transform: uppercase;
  font-size: 12px;
  letter-spacing: 1px;
}

.dropDownLink > ul {
  list-style: none;
  margin-bottom: 0;
  padding: 0;
}


.linkHeader {
  background-color: rgba(245,245,245,1);
  border-bottom: 1px solid rgba(250,250,250,0.1)
}

.dropDownList {
  padding: 0 auto;
}

.listItem {
  border-bottom: 1px solid rgba(0,0,0,0.1)

}



.body {
  width: 100vw;
  height: 900px;
  padding-top: 100px;
  background-image: url('https://ragingbull.com/wp-content/uploads/2019/08/DA-bg.jpg');
  background-position: top center;
  background-size: cover;
}

.mainContent {
  width: 80%;
  margin: 0 auto;
}

.mainContent > h1 {
  font-family: 'Montserrat',Helvetica,Arial,Lucida,sans-serif;
  color: white;
  font-size: 70px;
  font-weight: 800;
  text-transform: capitalize;
  text-align: center;
}

.mainContent > h1 > span {
  color: #41f900;
  text-transform: capitalize;
  padding: 0 25px;
}

.mainContent > h4 {
  color: white;
  text-align: center;
  font-weight: 800;

}

.kyleDennis {
  color: white;
  display: block;
  width: 100%;
  margin: 0 auto;
  letter-spacing: 5px;
  text-align: center;
}

.dollarAce {
  width: 100%;
}

.btn {
  background-color: #41f900;
  display: block;
  margin: 50px auto;
  width: 150px;
}

.footerContainer {
  height: 380px;
  width: 100%;
  display: flex;
  flex-direction: column;
  width: 100%;
  padding-top: 40px;
}

.footerLogo {
  flex: 1;
  margin: 0 auto;
}

.footerLinks {
  display: flex;
  flex-direction: row;
  width: 100%;
  padding: 3%;
  margin: 0 auto;
  justify-content: space-between;
}

.linksBox {
  flex: 1;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  list-style: none;
  padding: 0;
  margin: 0;
}

.linksBox > li {
  padding: 10px;
  font-size: 12px;
  letter-spacing: 2px;
  text-align: center;
}

.socialBox {
  flex: 1;
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.smIcons {
  height: 24px;
  padding: 0 10px;
}


.endNotes {
  font-size: 0.7em;
  width: 55%;
  margin: 0 auto;
  line-height: 30px;
  text-align: center;
}





@media only screen and (max-width: 975px) {

  .buttonsContainer {
    margin: 0;
    width: 100%;
  }

  .headerLinks {
    display: none;
  }

  .footerLinks {
    flex-direction: column;
    display: block;
  }

  .footerLogo {
    display: block;
    margin: 0 auto 100px

  }

  .linksBox {
    flex-direction: column;
    margin-bottom: 50px;
  }

  .linksBox > div {
    margin: 0 auto;
    font-size: 0.7em;
  }
}

@media only screen and (max-width: 840px) {
  .logo {
    height: 28px;
  }
  .body {
    height: 500px;
  }
  .mainContent > h1 {
    font-size: 1.5em;
  }
  .endNotes {
    width: 80%;
    line-height: 25px;
    padding-bottom: 20px;
  }
}
